@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.main-container{display:flex;height:100vh}.main-container .left{width:50%;display:flex;align-items:center;justify-content:center;background:linear-gradient(120deg, #6d47c7, white)}.main-container .left h1.company{font-size:60px}.main-container .right{width:50%;display:flex;align-items:center;justify-content:center;background:linear-gradient(120deg, #ff9800, white)}.main-container .right .login-container{border:1px solid #32a0a3;padding:53px;background:#00bcd4;box-shadow:15px 11px 11px -10px;border-radius:10px}.main-container .right .login-container h1{margin:10px 0px}.main-container .right .login-container .user-types{padding:5px;padding-left:0;width:200px}.main-container .right .login-container .user-types .user-type{font-size:20px;font-family:'Roboto', sans-serif;padding-left:5px}.main-container .right .login-container .submit-button{margin:5px;padding:5px 10px;outline:none;font-size:20px;font-family:'Roboto', sans-serif;background:linear-gradient(90deg, #00274f, #2a7395);border:1px solid #ffffff00;border-radius:5px;color:whitesmoke}

.admin-container h1{text-align:center}.admin-container .upload-window .upload-excel{margin:10px}.admin-container .upload-window .upload-excel .upload-tile{text-align:center;font-size:20px;margin-bottom:10px}.admin-container .upload-window .upload-excel .upload-button{text-align:center}.admin-container .upload-window .upload-excel .upload-button input{font-size:15px;width:200px;background:#d3d3d35c;padding:10px 25px;outline:none;border-radius:40px}.admin-container .upload-window .table-container{margin:auto;overflow:scroll;height:80vh;width:90vw}.admin-container .upload-window .table-container .row{display:flex}.admin-container .upload-window .table-container .row .element{min-width:200px;border:1px solid grey}

.nav-container{display:flex;justify-content:space-between;width:100%;padding:10px;margin-bottom:20px;background:#3571cd;align-items:center;justify-content:space-between;color:white;font-size:20px}.nav-container .right-section{display:flex;justify-content:space-between;width:120px;margin-right:10px}

.user-container{display:flex;width:100vw;flex-wrap:wrap;justify-content:center}.user-container .image-container{text-align:center}.user-container .image-container .image{width:200px;height:150px}.user-container .card{border:1px solid grey;margin:10px;padding:10px;border-radius:5px;background:#d3d3d38c;width:300px}

